import React, { Component } from "react";
import { Fade, Slide } from "react-reveal";

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      success: false,
      error: false,
      errorMessage: "",
      formData: {
        name: "",
        _replyto: "",
        subject: "",
        message: "",
      },
    };
  }

  handleInputChange = (e) => {
    const { name, value } = e.target;
    this.setState((prevState) => ({
      formData: {
        ...prevState.formData,
        [name]: value,
      },
    }));
  };

  handleSubmit = async (e) => {
    e.preventDefault();
    console.log("Form submission started");

    // Check if Formspree endpoint is configured
    if (!process.env.REACT_APP_FORMSPREE_ENDPOINT) {
      this.setState({
        error: true,
        errorMessage:
          "Form submission is not configured. Please contact the website administrator.",
      });
      return;
    }

    console.log(
      "Formspree endpoint:",
      process.env.REACT_APP_FORMSPREE_ENDPOINT
    );
    this.setState({
      loading: true,
      success: false,
      error: false,
      errorMessage: "",
    });

    try {
      const formData = new FormData();
      Object.entries(this.state.formData).forEach(([key, value]) => {
        formData.append(key, value);
      });

      // Show submitting message
      this.setState({
        loading: true,
        errorMessage: "Submitting your message...",
      });

      const response = await fetch(process.env.REACT_APP_FORMSPREE_ENDPOINT, {
        method: "POST",
        body: formData,
        headers: {
          Accept: "application/json",
        },
      });

      console.log("Response status:", response.status);
      const responseData = await response.json();
      console.log("Response data:", responseData);

      if (response.ok) {
        console.log("Submission successful");
        this.setState({
          loading: false,
          success: true,
          errorMessage: "",
          formData: {
            name: "",
            _replyto: "",
            subject: "",
            message: "",
          },
        });
      } else {
        console.log("Submission failed");
        this.setState({
          loading: false,
          error: true,
          errorMessage:
            responseData.error || "Failed to send message. Please try again.",
        });
      }
    } catch (error) {
      console.error("Submission error:", error);
      this.setState({
        loading: false,
        error: true,
        errorMessage:
          "Network error. Please check your connection and try again.",
      });
    }
  };

  render() {
    if (!this.props.data) return null;

    const name = this.props.data.name;
    const street = this.props.data.address.street;
    const city = this.props.data.address.city;
    const state = this.props.data.address.state;
    const zip = this.props.data.address.zip;
    const phone = this.props.data.phone;
    const message = this.props.data.contactmessage;

    return (
      <section id="contact">
        <Fade bottom duration={1000}>
          <div className="row section-head">
            <div className="two columns header-col">
              <h1>
                <span>Get In Touch.</span>
              </h1>
            </div>

            <div className="ten columns">
              <p className="lead">{message}</p>
            </div>
          </div>
        </Fade>

        <div className="row">
          <Slide left duration={1000}>
            <div className="eight columns">
              <form
                id="contactForm"
                name="contactForm"
                onSubmit={this.handleSubmit}
              >
                <fieldset>
                  <div>
                    <label htmlFor="contactName">
                      Name <span className="required">*</span>
                    </label>
                    <input
                      type="text"
                      value={this.state.formData.name}
                      onChange={this.handleInputChange}
                      size="35"
                      id="contactName"
                      name="name"
                      required
                      disabled={this.state.loading}
                    />
                  </div>

                  <div>
                    <label htmlFor="contactEmail">
                      Email <span className="required">*</span>
                    </label>
                    <input
                      type="email"
                      value={this.state.formData._replyto}
                      onChange={this.handleInputChange}
                      size="35"
                      id="contactEmail"
                      name="_replyto"
                      required
                      disabled={this.state.loading}
                    />
                  </div>

                  <div>
                    <label htmlFor="contactSubject">Subject</label>
                    <input
                      type="text"
                      value={this.state.formData.subject}
                      onChange={this.handleInputChange}
                      size="35"
                      id="contactSubject"
                      name="subject"
                      disabled={this.state.loading}
                    />
                  </div>

                  <div>
                    <label htmlFor="contactMessage">
                      Message <span className="required">*</span>
                    </label>
                    <textarea
                      cols="50"
                      rows="15"
                      value={this.state.formData.message}
                      onChange={this.handleInputChange}
                      id="contactMessage"
                      name="message"
                      required
                      disabled={this.state.loading}
                    ></textarea>
                  </div>

                  <div>
                    <button
                      type="submit"
                      className="submit"
                      disabled={this.state.loading}
                      style={{
                        position: "relative",
                        padding: "10px 20px",
                        backgroundColor: this.state.loading
                          ? "#666"
                          : "#0d0d0d",
                        color: "#fff",
                        border: "none",
                        cursor: this.state.loading ? "wait" : "pointer",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        gap: "10px",
                      }}
                    >
                      {this.state.loading ? (
                        <>
                          <i className="fa fa-spinner fa-spin"></i>
                          <span>Sending Message...</span>
                        </>
                      ) : (
                        "Submit"
                      )}
                    </button>
                  </div>
                </fieldset>
              </form>

              {this.state.loading && !this.state.error && (
                <div
                  style={{
                    color: "#666",
                    marginTop: "20px",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                  }}
                >
                  <i className="fa fa-spinner fa-spin"></i>
                  <span>Please wait while we send your message...</span>
                </div>
              )}

              {this.state.error && (
                <div
                  style={{
                    color: "red",
                    marginTop: "20px",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                  }}
                >
                  <i className="fa fa-exclamation-circle"></i>
                  <span>{this.state.errorMessage}</span>
                </div>
              )}

              {this.state.success && (
                <div
                  style={{
                    color: "green",
                    marginTop: "20px",
                    display: "flex",
                    alignItems: "center",
                    gap: "10px",
                  }}
                >
                  <i className="fa fa-check-circle"></i>
                  <span>
                    Your message has been sent successfully! You should receive
                    a confirmation email shortly.
                  </span>
                </div>
              )}
            </div>
          </Slide>

          <Slide right duration={1000}>
            <aside className="four columns footer-widgets">
              <div className="widget widget_contact">
                <h4>Contact Details</h4>
                <p className="address">
                  {name}
                  <br />
                  {city}, {state} {zip}
                  <br />
                  <span>Email: mthines2003@gmail.com</span>
                </p>
              </div>
            </aside>
          </Slide>
        </div>
      </section>
    );
  }
}

export default Contact;
